
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import {BioComponent} from './bio/bio.component';



import {BlobsComponent} from "./blobs/blobs.component";
import {FormulaireComponent} from "./formulaire/formulaire.component";
import {BioColorComponent} from "./bio/bio-color/bio-color.component";



const routes: Routes = [
  { path: '', component: BioComponent },
  { path: 'contact', component: FormulaireComponent },
  { path: 'blobs', component: BlobsComponent },
  { path: 'jour', component: BioColorComponent }

];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule { }

