import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, FormGroupDirective} from "@angular/forms";
import {PostService} from "../services/post.service";

@Component({
  selector: 'app-formulaire',
  templateUrl: './formulaire.component.html',
  styleUrls: ['./formulaire.component.scss']
})
export class FormulaireComponent implements OnInit {
  creationForm: FormGroup;

  constructor(private fb: FormBuilder, private postService: PostService) {
  }

  ngOnInit() {
    this.createForm();
  }

  createForm() {
    this.creationForm = this.fb.group({
      nom : '',
      prenom: '',
      mail: '',
      objet: '',
      message: '',
    })
  }

  createPost(formDirectiv: FormGroupDirective) {
    if (this.creationForm.valid) {
      console.log(this.creationForm);
      this.postService
        .createPost(this.creationForm.value)
        .subscribe(data => this.handleSuccess(data, formDirectiv), error => this.handleError(error));
    }
  }

  handleSuccess(data, formDirectiv) {
    console.log('OK handleSuccess - message envoyé !', data);
    this.creationForm.reset();
    formDirectiv.resetForm();
    this.postService.dispatchpostCreated(data._id);
  }

  handleError(error) {
    console.log('KO handleError - message pas envoyé vers db', error);
  }

}
