import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import {Observable, Subject} from "rxjs";

import { Post } from '../models/post';

@Injectable({
  providedIn: 'root'
})
export class PostService {
  baseUrl = 'http://localhost:1337/blob-posts';
  private postCreated = new Subject<string>();

  constructor(private _httpClient: HttpClient) {
  }

  getPost(): Observable<Post[]> {
    return this._httpClient.get<Post[]>(`${this.baseUrl}/`);
  }

  getPostById(id: string): Observable<Post> {
    return this._httpClient.get<Post>(`${this.baseUrl}/${id}`);
  }

  createPost (post: Post) {
    return this._httpClient.post<Post>(this.baseUrl, post);
  }

  dispatchpostCreated(id: string) {
    this.postCreated.next(id);
  }

}

